html {
    box-sizing: border-box;
}

*,*::before,
*::after {
box-sizing: inherit;
}


body {
    margin: 0;
    padding: 0;
      
    font-family: 'Roboto-Regular', sans-serif;
    font-size: 15px;
    line-height: 24px;
    font-weight: 400;
    color: @text;
}
img {
    max-width: 100%;
    height: auto;
}
a{
    display: inline-block;
    text-decoration: none;
    color: @text;
}
ul,
li{
    margin: 0;
    padding: 0;
    list-style: none;
}
.container{
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
}
.container-fluid{
    max-width: 100%;
    margin: 0 auto;
    padding: 0 30px;
}

@media (max-width: 720px) {
    .container-fluid{
        padding: 0 15px;
    }
}

.title{
    text-align: center;
    color: @bg;
    font-weight: 700;
    font-size: 36px;
    line-height: 60px;
    margin-bottom: 30px;
}
.rate-star{
    display: inline-block;
    margin-right: 13px;
}