.products__slider{
    padding: 110px 0 120px;
    .slick-dots{
        text-align: center;
        margin-top: 50px;
        li{
            display: inline-block;
            margin-left: 3px;
            margin-right: 3px;
            &.slick-active{
                button{
                    background-color: @green;
                }
            }
            button{
                border-radius: 50%;
                border: none;
                font-size: 0;
                background-color: rgb(232, 232, 232);
                width: 16px;
                height: 16px;
                cursor: pointer;
                outline: none;
            }
        }
    }
}